@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);
:root {
  /* Color */
  --color-white: #ffffff;
  --color-light-white: #eeeeee;
  --color-dark-white: #bdbdbd;
  --color-pink: #fe918d;
  --color-dark-pink: #ff6863;
  --color-black: #000000;
  --color-dark-grey: #4d4d4d;
  --color-grey: #7e7e7e;
  --color-light-grey: #9e9e9e;
  --color-blue: #73aace;
  --color-yellow: #fff7d1;
  --color-light-orange: #fdde70;
  --color-orange: #feb546;
  --color-deep-orange: #ff9900;
  --color-blue: #5ea7fa;
  --color-deep-blue: #0026ff;

  /* Font size */
  --font-very-large: 50px;
  --font-large: 38px;
  --font-medium: 24px;
  --font-regular: 18px;
  --font-small: 16px;
  --font-micro: 12px;
  --font-nano: 10px;

  /* Font Weight */
  --weight-bold: 700;
  --weight-semi-bold: 600;
  --weight-regular: 400;

  /* Size */
  --size-border-radius: 4px;

  /* Animation */
  --anmation-duration: 300ms;
}

/* Universal tags */
* {
  box-sizing: border-box;
}

/* font */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: default;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding-left: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 44px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.subTitle {
  color: #868e96;
}

#navbar {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 12px;
  transition: all var(--anmation-duration) ease-in-out;
  z-index: 2;
}

.navbar__menu {
  display: flex;
  margin: 4px 0px;
}

.navbar__logo__title {
  display: inline-block;
  padding: 6px 12px;
  color: var(--color-black);
  font-size: var(--font-large);
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 500;
}

.navbar__menu__item {
  padding: 8px 12px;
  margin: 0 10px;
  color: var(--color-black);
  border-radius: var(--size-border-radius);
  cursor: pointer;
  border: 1px solid transparent;
  font-size: var(--font-regular);
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  #navbar {
    flex-direction: column;
    align-items: flex-start;
  }
  .navbar__menu {
    flex-direction: column;
    text-align: center;
    width: 100%;
    /* display: none; */
  }
}

#main {
  margin-top: 0;
}
#greeting {
  width: 100%;
  padding: 20px 10px;
  margin: 0px auto;
  padding-top: 4rem;
  position: relative;
  z-index: 1;
}

#greeting:after {
  background-image: url(/static/media/GreetingBackground.effbbb5c.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  position: absolute;
  background-size: cover;
  opacity: 0.3 !important;
  z-index: -1;
  filter: alpha(opacity=50);
  content: "";
  width: 100%;
  height: 100%;
}

.greeting__main {
  width: 100%;
  margin-top: 5rem;
  display: flex;
  margin-bottom: 30px;
}
.greeting__main > * {
  flex: 1 1;
}

.greeting__text {
  font-size: var(--font-large);
}
.greeting__text__container {
  padding: 30px;
  padding-left: 80px;
  display: flex !important;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.greeting__text__subtitle {
  color: #000000 !important;
  font-size: var(--font-regular);
  line-height: 40px;
  font-weight: 600;
}

.greeting__text__button {
  display: flex;
  margin-top: 20px;
  justify-content: center;
}

.greeting__img__container {
  padding: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.greeting__img__container > img {
  max-width: 50%;
  height: auto;
  border-radius: 100%;
}

@media screen and (max-width: 768px) {
  .greeting__text__container {
    padding: 30px !important;
  }
  .greeting__text__button {
    justify-content: space-around;
  }
  .greeting__text {
    font-size: var(--font-regular);
    text-align: center;
  }
  .greeting__text__subtitle {
    font-size: var(--font-small);
  }
  .greeting__main {
    display: block;
  }
  .greeting__img__container {
    height: 256px;
  }
  .greeting__img__container > img {
    height: 100%;
  }
}

@media screen and (max-width: 320px) {
  #greeting {
    width: auto;
  }
}

.main-button {
  background-color: var(--color-orange);
  color: white;
  border-radius: 4px;
  font-size: var(--font-regular);
  cursor: pointer;
  transition: all var(--anmation-duration) ease-in-out;
  margin: 0px 10px;
  padding: 5px 10px;
  font-weight: 500;
}

.main-button:hover {
  background-color: #ffffff;
  color: var(--color-deep-orange);
  transition: all var(--anmation-duration) ease;
  transform: translateY(-3px);
}

@media screen and (max-width: 768px) {
  .main-button {
    font-size: var(--font-small);
    padding: 12px 18px;
  }
}

@media screen and (max-width: 320px) {
  .main-button {
    font-size: var(--font-micro);
  }
}

.dev-icons {
  padding-left: 0;
  list-style: none;
  font-size: 5rem;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
}
.software__skills__item {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
}

.software__skills__item > i {
  color: #868e96;
}

.software__skills__item > i:hover {
  color: var(--color-deep-orange);
}
.software__skills__item > p {
  color: #868e96;
  font-size: var(--font-regular);
}
.software__skills__item > i:hover ~ p {
  color: var(--color-deep-orange);
}

.skills__main {
  display: flex;
}

.skills__main > * {
  flex: 1 1;
  margin-bottom: 30px;
}

.skills__img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.skills__img > img {
  max-width: 100%;
  height: auto;
  padding: 2rem;
}

.skills__subtitle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

.skills__subtitle__text {
  font-size: var(--font-regular);
}

.subtitle__text {
  font-size: var(--font-small);
}

@media screen and (max-width: 768px) {
  .skills__main {
    flex-direction: column;
  }

  .skills__main > * {
    margin-bottom: 10px;
  }

  .skills__img > img {
    max-width: 100%;
    height: auto;
    padding: 0;
  }

  .skills__subtitle__text {
    font-size: var(--font-small);
  }
  .dev-icons {
    font-size: 3rem;
  }
  .subtitle__text {
    font-size: var(--font-micro);
  }
}

